import React, { useState, useEffect } from 'react'
import Filter from './components/Filter'
import PersonForm from './components/PersonForm'
import Phonebook from './components/Phonebook'
import phoneService from './services/phones'
import Notification from './components/Notification'


const App = () => {

  const [ persons, setPersons ] = useState([]) 

  useEffect( ()=> {
    phoneService.getAll()
    .then(data =>{
      setPersons(data)
    })
    }, [])



  const [ newName, setNewName ] = useState('')
  const [ newPhone, setNewPhone ] = useState('')
  const [filter, setFilter ] = useState('')
  const [notifyMessage, setNotifyMessage] = useState('')
  const [notifyStyle, setNotifyStyle] = useState('')

  const handleNameChange = (e) =>{ 
    setNewName(e.target.value)
  }

  const handlePhoneChange = (e) =>{
    setNewPhone(e.target.value)
  }

  
  const handleFilter = (e) =>{
    setFilter(e.target.value)
    
  }

  const handleDelete = (id, name) =>{
    window.confirm(`Delete ${name}?`)
    && phoneService.remove(id)
    .then(()=>{
      const newPersons = persons.filter(person => person.id !== id)
      setPersons(newPersons)
      setNotifyMessage(`Successfully deleted ${name}`)
      setNotifyStyle('success')
      setTimeout(()=>{
        setNotifyStyle('')
      },3000)
      }
    )
    .catch(err=>{
      setNotifyMessage(`Information of ${name} has already been removed from the system`)
      setNotifyStyle('error')
    })
  }



  const handleSubmit = (e) => {
    e.preventDefault()
    const newObject = {
      name: newName, 
      phone: newPhone}

    const personToChange = persons.find(person => person.name === newName)
    const personToUpdate = {...personToChange, phone: newPhone}

    if (personToChange){
      window.confirm(`${newName} is already added to the phonebook, replace the old number with a new one?`)
      && phoneService
      .update(personToChange.id, personToUpdate)
      .then(returnedPerson => {
        setPersons(
          persons.map(person=>
            person.id !== personToChange.id ? person : returnedPerson
          )
        )
        setNotifyMessage(`Updated ${returnedPerson.name}`)
        setNotifyStyle('success')
        setTimeout(()=> {
          setNotifyStyle('')
        },3000)

      })
      .catch(err =>{
          if (err.response.status === 406) {
          setNotifyMessage(JSON.stringify(err.response.data.error).replace(/['"]+/g, ''))
          setNotifyStyle('error')
          setTimeout(()=>setNotifyStyle(''),3000)
          } else {
          setNotifyMessage(`Information of ${newName} has already been removed from the system`)
          setNotifyStyle('error')
          setTimeout(()=> setNotifyStyle(''),3000)
          }
        })
      } else {
        phoneService
        .create(newObject)
        .then(newPerson => {
        setPersons(persons.concat(newPerson))
        setNotifyMessage(`Added ${newPerson.name}`)
        setNotifyStyle('success')
        setTimeout(() => setNotifyStyle(''), 3000)
        })
        .catch( err => {
        setNotifyMessage(JSON.stringify(err.response.data.error).replace(/['"]+/g, ''))
        setNotifyStyle('error')
        setTimeout(() => setNotifyStyle(''), 3000)
        })
        setNewName('')
        setNewPhone('')
      }
  }
 


  return (
    <div>
      <h2>Phonebook</h2>
      {notifyStyle && <Notification message={notifyMessage} style = {notifyStyle}/>}
      <Filter handleFilter={handleFilter} filter={filter}/>
      <div>
        <h2>add a new</h2>
        <PersonForm
          newName = {newName}
          handleNameChange = {handleNameChange}
          newPhone = {newPhone}
          handlePhoneChange = {handlePhoneChange}
          handleSubmit = {handleSubmit}         
        />
      </div>
      <div>
        <h2>Numbers</h2>
          <Phonebook filter ={filter} persons = {persons} handleDelete={handleDelete}/>
      </div>
    </div>
  )
}

export default App